.contact {

    &__info, 
    &__form {
        padding: 30px;
        @media (min-width: $screen-sm) {
           padding: 80px;
        }
        @media (min-width: $screen-md) {
            padding: 100px;
        }
    }
    
    form {
        display: flex;
        flex-direction: column;
    }
}